<template>
  <v-toolbar>
    <img src="@/assets/logo.png" style="height: 60%" />
    <v-spacer></v-spacer>
    <v-toolbar-items>
    <!-- <v-toolbar-items class="hidden-sm-and-down"> -->
      <v-btn text @click="ingresarCursos">
        <span class="mr-2">Mis Cursos</span>
      </v-btn>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-account-circle</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item @click="ingresarCursos()">
            <v-list-item-title>Mis Cursos</v-list-item-title>
          </v-list-item>
          <v-list-item @click="ingresarPerfil()">
            <v-list-item-title>Mi Perfil</v-list-item-title>
          </v-list-item>
          <v-divider></v-divider>
          <v-list-item @click="cerrarSesion()">
            <v-list-item-title>
              <v-icon class="mr-2">mdi-power</v-icon>Cerrar Sesión
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
export default {
  methods: {
    ingresarTest() {
      this.redireccionar("test");
    },
    ingresarPerfil() {
      this.redireccionar("perfil");
    },
    ingresarCursos() {
      this.redireccionar("cursos");
    },
    cerrarSesion() {
      this.$store.state.servicio.sesion.cerrarSesion();
      this.redireccionar("login");
    },
    redireccionar(nombre) {
      if (this.$router.history.current.name != nombre)
        this.$router.push({ name: nombre });
    }
  }
};
</script>

<style>
.v-list {
  padding: 0;
}
</style>
